.center {
    text-align: center;
}

.pagination {
    display: inline-block;
    list-style-type: none;
}

.pagination li {
    color: #3f51b5;
    float: left;
    padding: 4px 12px;
    text-decoration: none;
    transition: background-color .3s;
    border: 1px solid rgba(63, 81, 181, 0.5);
    border-radius: 5px;
    margin: 0 4px;
    cursor: pointer;
}

.pagination li.active {
    background-color: #3f51b5;
    color: white;
    border: 1px solid #3f51b5;
    cursor: auto;
}

.pagination li.disable {
    background-color: #D3D3D3;
    opacity: 0.7;
    color: white;
    border: 1px solid #D3D3D3;
    cursor: not-allowed;
}

.pagination li.dot {
    background-color: white;
    opacity: 0.7;
    color: #3f51b5;
    border: 1px solid #3f51b5;
    cursor: not-allowed;
}

.pagination li:hover:not(.active):not(.disable):not(.dot) {
    background-color: #3f51b5;
    color: white;
}